import lotusAddress from "../../components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue"
import {
	ajax
} from '../../common/js/util.js'
import {
	Toast,
	Dialog
} from 'vant'
export default {
	data() {
		return {
			Color: '#DE2910',
			value: '',
			backColor: '#FFFFFF',
			show: false,
			lotusAddressData: {
				visible: false,
				provinceName: '',
				cityName: '',
				townName: '',
			},
			region: '',
			location: '',
			card: {},
			list: {},
			address: '',
			amount: 0,
			appId: '',
			timeStamp: '',
			nonceStr: '',
			packages: '',
			signType: '',
			paySign: ''
		}
	},
	components: {
		"lotus-address": lotusAddress,
		Toast
	},
	onLoad() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
	},
	onShow() {
		console.log(this.$store.state.vipType)
		this.init()
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				uid: this.$store.state.uid
			}
			let data = {
				url: '/api/gzh/vipCard',
				data: datas,
				success: function(res) {
					if (res.data.result == 0) {
						console.log(res)
						for (let i = 0; i < res.data.dataList.length; i++) {
							if (res.data.dataList[i].cardType == self.$store.state.vipType) {
								self.list = res.data
								self.card = res.data.dataList[i]
							}
						}
					}
				}
			}
			ajax(data)
		},
		//打开picker
		openPicker() {
			this.lotusAddressData.visible = true;
		},
		//回传已选的省市区的值
		choseValue(res) {
			this.lotusAddressData.visible = res.visible; //visible为显示与关闭组件标识true显示false隐藏
			//res.isChose = 1省市区已选 res.isChose = 0;未选
			if (res.isChose) {
				this.lotusAddressData.provinceName = res.provice; //省
				this.lotusAddressData.cityName = res.city; //市
				this.lotusAddressData.townName = res.town; //区
				this.region = `${res.provice} ${res.city} ${res.town}`; //region为已选的省市区的值
			}
			console.log(this.region)
		},
		open() {
			uni.navigateTo({
				url: '../opened/opened'
			});
		},
		bindCard() {
			uni.navigateTo({
				url: '../bind/bind'
			});
		},
		submit() {
			let self = this
			if (this.region == '') {
				Toast('请选择地址')
				return
			}

			if (this.location == '') {
				Toast('地址详情不能为空')
				return
			}
			
			this.address = this.region + '' + this.location
			let datas = {
				url: '/api/gzh/replacement',
				data: {
					uid: this.$store.state.uid,
					address: self.address
				},
				success: function(res) {
					console.log(res)
					if (res.data.result == 0) {
						self.amount = res.data.amount
						self.appId = res.data.body.appId
						self.timeStamp = res.data.body.timeStamp
						self.nonceStr = res.data.body.nonceStr
						self.packages = res.data.body.prepay
						self.signType = res.data.body.signType
						self.paySign = res.data.body.paySign
						self.show = true
					} else {
						Toast(res.data.resultNote)
					}
				}
			}
			ajax(datas)
		},
		confirm() {
			let self = this
			self.onBridgeReady(self.appId, self.timeStamp, self.nonceStr, self.packages, self.signType, self.paySign)
		},
		onBridgeReady(appId, timeStamp, nonceStr, packages, signType, paySign) {
			let self = this
			WeixinJSBridge.invoke(
				'getBrandWCPayRequest', {
					'appId': appId,
					'timeStamp': timeStamp,
					'nonceStr': nonceStr,
					'package': packages,
					'signType': signType,
					'paySign': paySign
				},
				function(res) {
					if (res.err_msg === 'get_brand_wcpay_request:ok') {
						self.show = false
						uni.navigateBack({
							delta: 1
						})
					} else if (res.err_msg === 'get_brand_wcpay_request:cancel') {
						Toast('用户取消支付')
					} else if (res.err_msg === 'get_brand_wcpay_request:fail') {
						Toast('网络异常，请重试')
					}
				}
			)
		}
	}
}
